},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"about-future-vision",staticStyle:{"width":"100%","color":"gray","font-size":"15px"}},[_c('h4',[_vm._v("臺灣當代文化實驗場（C-LAB）自 2020 年正式啟動「科技媒體實驗平台」，著重於媒體實驗創新與社會鏈結，以跨域、共創、協作方式，打造文化與科技的實驗平台。 整合臺灣文化與科技的資源，媒合文化與科技領域共創，其中「未來視覺實驗室」探索科技媒體的視覺極限為實驗目標，以臺灣光電技術與產業為基礎，輔以影像的運算、創作與軟硬體整合，企圖勾勒出未來視覺的跨域願景。 自計畫開創，科技媒體實驗平台便持續推動實驗展演計畫「FUTURE VISION LAB」，以數位實驗建築著手打造 DOME 型態場域，培育跨域實驗人才，打造國內年輕創作者展演內容創造力的舞台，同步引介國外精彩作品。 本展演計畫以探索科技媒體的視覺極限為目標，其開發涉及球形曲面投影的校正、融接、對位、播放控制與影像前製等多項複雜技術，過去三年已進行近百件作品展演。 「FUTURE VISION LAB 2023」實驗展演計畫，今年突破過往尺度，開啟「DOME 2.0」升級計畫，重新打造升級版的戶外穹頂劇場（DOME），延續多元及實驗精神，以數位建築發展更具演算特色之外型，尺寸突破過往直徑 12 公尺之尺度， 重新研發打造直徑 15公尺移動式戶外穹頂劇場及更包覆式聲場配置，並研發新型態投影膜片及超高解析沉浸影像環境，總運算解析達 22 K，打造接軌國際規格的創作環境，讓觀眾全天候享受更高品質的沉浸式感官內容，感受臺灣的創造力。 「FUTURE VISION LAB 2023」於臺灣文博會展期結束後，將重新規劃第二階段的展演，展期為 10 月 14 日至 12 月 24 日，內容除文博會期間展出之節目內容， 更將新增扶植培育創作者參與的實驗計畫，作品精彩可期，節目內容將於文博會展期後公布，敬請期待。")])])
